@use '../utils/mixins' as *;

.form {
  display: flex;
  flex-direction: column;
  gap: 16px;

  &__label {
    &:not(:focus):not(:active) {
      @include visually-hidden();
    }
  }

  &__input {
    border-radius: 40px;
    background-color: #f8f8fa;
    border: 0;
    width: 100%;
    height: 48px;
    line-height: 48px;
    padding-left: 16px;
    box-sizing: border-box;

    &::placeholder {
      font-family: 'Inter';
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;

      color: #7e7e83;
    }
  }

  &__textarea {
    border-radius: 16px;
    background-color: #f8f8fa;
    border: 0;
    width: 100%;
    line-height: 48px;
    padding: 14px 16px 14px 16px;
    box-sizing: border-box;

    &::placeholder {
      font-family: 'Inter';
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;

      color: #7e7e83;
    }
  }
}
