@use '../utils/mixins' as *;

.main {
  padding: 64px 20px;

  font-family: 'Inter';
  font-size: 16px;
  font-weight: 400;
  line-height: 27px;
  text-align: left;

  @include grid();

  &__paragraph {
    &--bold {
      font-family: 'Inter';
      font-size: 16px;
      font-weight: 700;
      line-height: 22.4px;
      text-align: left;

      margin-bottom: 16px;
    }
  }

  &__title {
    margin-bottom: 48px;
    grid-column: 1 / 3;

    @include for-tablet {
      grid-column: 1 / 4;
    }

    @include for-desktop() {
      grid-column: 1 / 7;
    }
  }

  &__content {
    padding-top: 64px;
    grid-column: 1 / 3;
    margin-bottom: 96px;

    @include for-tablet {
      grid-column: 4 / 7;
    }

    @include for-desktop() {
      grid-column: 7 / 13;
    }
  }

  &__section {
    grid-column: 1 / 3;

    @include for-tablet() {
      grid-column: 1 / 7;
    }

    @include for-desktop() {
      grid-column: 1 / 13;
    }
  }

  &__text {
    &--space {
      margin-bottom: 40px;
    }
  }
}
