@use '../utils/mixins' as *;

.header {
  background-image: url('images/header-background.jpg');
  background-color: rgb(175, 175, 175);
  background-repeat: no-repeat;
  background-size: cover;

  min-height: 100vh;
  padding: 0 20px 40px;
  box-sizing: border-box;

  @include grid();

  &__navigation {
    align-self: start;
    grid-column: 1 / 3;
    grid-row: 1 / 2;

    @include for-tablet {
      grid-column: 1 / 7;
    }

    @include for-desktop() {
      grid-column: 1 / 13;
    }
  }

  &__title {
    align-self: end;
    grid-column: 1 / 3;
    grid-row: 2 / 3;

    @include for-tablet {
      grid-column: 1 / 7;
    }

    @include for-desktop() {
      grid-column: 1 / 13;
    }
  }
}
