.product {
  &__image {
    display: block;
    margin: 0 auto 32px;
  }

  &__name {
    font-family: 'Inter';
    font-size: 16px;
    font-weight: 700;
    line-height: 22.4px;
    text-align: left;

    color: #292929;

    margin-bottom: 8px;
  }

  &__item {
    font-family: 'Inter';
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    text-align: left;

    color: #292929;

    margin-bottom: 12px;
  }

  &__price {
    font-family: 'Inter';
    font-size: 16px;
    font-weight: 700;
    line-height: 22.4px;
    text-align: left;

    color: #292929;
  }
}
