@use '../utils/mixins' as *;

.phone {
  &__label {
    font-family: 'Inter';
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;

    color: #292929;
    position: absolute;
    transform: translateX(calc(-100% - 20px)) translateY(5px);
  }

  &__icon {
    width: 18px;
    height: 18px;

    @include for-tablet() {
      width: 24px;
      height: 24px;
    }

    @include for-desktop() {
      width: 24px;
      height: 24px;
    }
  }

  &:not(:hover) &__label {
    @include visually-hidden();
  }
}
