@use '../utils/mixins' as *;

.pictures {
  @include grid();

  &__images {
    width: 100%;
    position: relative;
    padding-top: 100%;

    @include for-tablet() {
      grid-column: span 3;
      padding-top: 184px;
      padding-left: 0;
    }

    @include for-desktop() {
      grid-column: span 6;
      padding-top: 324px;
      padding-left: 0;
    }

    &--small {
      @include for-tablet() {
        grid-column: span 2;
      }

      @include for-desktop() {
        grid-column: span 4;
      }
    }

    &--big {
      @include for-tablet() {
        grid-column: span 4;
      }

      @include for-desktop() {
        grid-column: span 8;
      }
    }
  }

  &__items {
    top: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;

    &--move {
      object-position: 95%;
    }
  }
}
