@use '../utils/mixins' as *;

.menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(100%);
  transition: transform 0.4s ease-in-out;
  background-color: #e8eff9;
  padding: 0 20px 40px;

  min-height: 100vh;

  @include grid();

  align-content: start;

  &:target {
    transform: translateX(0);
  }

  &__navigation {
    grid-column: span 2;

    @include for-tablet() {
      grid-column: span 6;
    }

    @include for-desktop() {
      grid-column: span 12;
    }
  }

  &__links {
    margin-bottom: 40px;
    grid-column: span 2;

    @include for-tablet() {
      grid-column: span 6;
    }

    @include for-desktop() {
      grid-column: span 12;
    }
  }

  &__paragraph {
    font-family: 'Inter';
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    text-align: left;
    margin-bottom: 16px;

    color: #292929;

    grid-column: span 2;

    @include for-tablet() {
      grid-column: span 6;
    }

    @include for-desktop() {
      grid-column: span 12;
    }
  }

  &__call-link {
    font-family: 'Inter';
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 2px;
    text-align: left;
    text-transform: uppercase;
    text-decoration: none;

    color: #292929;

    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    &:hover {
      border-bottom: 1px solid #292929;
    }
  }
}
