@use '../utils/mixins' as *;

.button {
  border-radius: 40px;
  background-color: #292929;
  display: block;
  text-decoration: none;
  font-family: 'Inter';
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  text-align: center;
  padding: 15px 0;
  border: 3px solid #292929;

  margin-bottom: 48px;

  color: #ffffff;

  &:hover {
    background-color: #ffffff;
    color: #292929;
    border: 3px solid #292929;
  }

  &:active {
    color: #ffffff;
    background-color: #111111;
  }

  &--primary {
    border-radius: 40px;
    border: 3px solid #292929;

    font-family: 'Inter';
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    text-align: center;
    background-color: #292929;
    color: #ffffff;

    padding: 15px 0;

    &:hover {
      background-color: #ffffff;
      color: #292929;
      border: 3px solid #292929;
    }

    &:active {
      color: #ffffff;
      background-color: #111111;
    }
  }
}
