@use '../utils/mixins' as *;

.section {
  padding-top: 64px;

  &__paragraph {
    font-family: 'Inter';
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
    text-align: left;

    color: #292929;

    &--bold {
      font-family: 'Inter';
      font-size: 16px;
      font-weight: 700;
      line-height: 22.4px;
      text-align: left;

      margin-bottom: 16px;
    }
  }

  &__article {
    margin-bottom: 40px;
  }
}
