.title {
  &--decoration {
    font-family: 'Inter';
    font-size: 32px;
    font-style: italic;
    font-weight: 800;
    line-height: 32px;
    letter-spacing: -1.5px;
    text-align: left;

    color: #292929;

    @include for-tablet() {
      font-size: 40px;
      line-height: 40px;
    }

    @include for-desktop() {
      font-size: 48px;
      line-height: 48px;
    }

    &::before {
      content: '';
      display: block;
      background-image: url('images/waves.svg');
      background-repeat: no-repeat;
      width: 48px;
      height: 58px;
      background-size: cover;
      margin-bottom: 16px;
    }
  }

  &--section {
    font-family: 'Inter';
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: -1.5px;
    text-align: center;

    color: #292929;

    @include for-tablet() {
      font-size: 40px;
      line-height: 40px;
    }

    @include for-desktop() {
      font-size: 48px;
      line-height: 48px;
    }
  }

  &--secondary {
    font-family: 'Inter';
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: -1.5px;
    text-align: left;

    color: #292929;

    padding-top: 64px;

    @include for-tablet() {
      font-size: 40px;
      line-height: 40px;
    }

    @include for-desktop() {
      font-size: 48px;
      line-height: 48px;
    }
  }
}
